import { Component } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActiveModalRef, DismissReasons } from '@bmc-ux/adapt-angular';
import { DevelopmentMode } from '@helix/platform/shared/api';
import { map, startWith } from 'rxjs/operators';

@Component({
  selector: 'rx-development-mode-selector',
  templateUrl: './development-mode-selector.component.html'
})
export class DevelopmentModeSelectorComponent {
  developmentModeFormControl = new FormControl(this.activeModalRef.getData().developmentMode);

  isSaveButtonDisabled$ = this.developmentModeFormControl.valueChanges.pipe(
    startWith(this.activeModalRef.getData().developmentMode),
    map((value: DevelopmentMode) => value === this.activeModalRef.getData().developmentMode)
  );

  constructor(private activeModalRef: ActiveModalRef) {}

  closeModal(): void {
    this.activeModalRef.dismiss(DismissReasons.CLOSE_BTN);
  }

  selectDevelopmentMode(): void {
    this.activeModalRef.close(this.developmentModeFormControl.value);
  }
}
